let key = {
    paintBucket: 'KeyB',
    chooseColor: 'KeyC',
    pencil: "KeyP",
    stroke: "KeyS",
    eraser: "KeyE",
    bucket: "KeyA",
  }

function swapKeys(){
    let elements = document.getElementsByClassName('inputKey');
    let keyNow = Object.keys(key);
    for(let i = 0; i < elements.length; i++){
    if(elements[i].value > 0 && 10 > elements[i].value) key[keyNow[i]] = `Digit${elements[i].value}`;
    else key[keyNow[i]] = `Key${elements[i].value}`;
    }
    document.getElementById('swapKeys').style.display = 'none';
    return key;
}


export { swapKeys, key }